.skill-container {
  width: 100%;
  color: #fff;
  padding-top: 40px;
  padding-bottom: 80px;
  background-color: #121212;
  border-radius: 20px 20px 0px 0px;
}
.skill-container .wrapper {
  width: 90%;
  margin: auto;
}
#skills-title {
  font-size: 50px;
}
.list-skills h3 {
  font-size: 40px;
}
.list-skills h2 {
  font-size: 30px;
}
.list-skills ul {
  font-size: 20px;
  padding: 0;
}
.list-skills ul li {
  width: 100%;
  text-transform: capitalize;
}
