.list-component {
  width: 100%;
  padding-top: 40px;
  background-color: #121212;
}
.list-component .wrapper {
  width: 90%;
  margin: auto;
}
.list-component .wrapper h1 {
  font-size: 50px;
  color: white;
}

.list-project {
  text-align: center;
  padding-bottom: 100px;
  background-color: #121212;
}

.project-link {
  text-decoration: none;
}
.slider-item {
  width: 100%;
  color: #fff;
  padding-bottom: 15px;
  border-radius: 10px;
  transition: all 0.7s;
}
.slider-item .project-logo {
  width: 100%;
  height: 200px;
  margin-bottom: 10px;
  border-radius: 10px 10px 10px 10px;
}
.slider-item .project-title {
  font-size: 16px;
  padding: 10px 5px 0px 5px;
}
.slider-item .project-description {
  overflow: scroll;
  width: 90%;
  height: 50px;
  color: white;
  resize: none;
  padding: 4px;
  border: none;
  border-radius: 6px;
  background-color: #121212;
  margin-bottom: 6px;
}
.slider-item .project-description:focus {
  outline: none;
}
.button-container {
  margin-top: 15px;
}
