@keyframes headerEffectIn {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

header {
  top: 0;
  width: 100vw;
  position: fixed;
  z-index: 10;
  padding-top: 20px;
  animation: 0.8s ease-in-out 0s 1 headerEffect;
}
header div.container {
  position: relative;
  width: 92%;
  max-height: 30px;
  padding: 17px 2%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  border-radius: 35px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: -1px 0px 14px -1px rgba(0, 0, 0, 0.52);
}
header div.container div.col-m-3 {
  flex-flow: column;
  flex: 1;
}
header div.container div.col-m-3 span {
  position: relative;
  margin-top: 5px;
}
header div.container div.col-m-3 .noti {
  position: relative;
  margin-top: -2px;
}
header div.container div.col-m-3 #logo {
  margin-top: -20px;
}

@media only screen and (max-width: 768px) {
  header div.container {
    width: 80%;
    padding: 17px 4%;
  }
  .header-alerts {
    display: none;
  }
  .no-mobile {
    display: none;
  }
  .text-center {
    text-align: left;
  }
}
