@media only screen and (max-width: 768px) {
  .home-msg .msg p {
    width: 100% !important;
  }
  .skills .row {
    display: block;
  }
  #techs {
    margin-top: 30px;
  }
}

section .home-msg {
  display: block;
  width: 100vw;
  height: 100vh;
}
section .home-msg .msg {
  position: absolute;
  z-index: 0;
  bottom: 0;
  color: #fff;
  padding: 60px 30px;
}
section .home-msg .msg h2 {
  font-size: 50px;
}
section .home-msg .msg p {
  display: block;
  width: 35vw;
}
section .home-msg .msg .divider {
  border-top: 1px solid #fff;
  width: 100px;
}
hr {
  color: transparent;
  background-color: transparent;
  border: none;
  height: 0px;
  margin: 2px 0px;
}
