@keyframes navBarEffectIn {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes navBarEffectOut {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(100%);
  }
}

@media only screen and (max-width: 768px) {
  .no-mobile {
    display: none;
  }
  .social-media nav {
    width: 100% !important;
  }
}

#navBar {
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100vw;
  height: 100vh;
  max-height: 100%;
  overflow: hidden;
  background-color: #1d1d1d;
  transition: all ease-in-out 0.5s;
}

#navBar-container {
  width: 95%;
  height: 100%;
  margin: auto;
}

#navBar nav {
  margin-top: 20%;
}
#navBar ul {
  list-style: none;
  width: 90%;
  margin: auto;
}
#navBar ul li {
  display: block;
  color: #fff;
  margin-bottom: 10px;
}
#navBar ul li span.title {
  display: block;
  font-size: 26px;
  color: #b5b5b5;
  padding-bottom: 20px;
}
#navBar ul li a.menu-item {
  font-size: 30px;
  color: #fff;
  text-decoration: none;
}
#navBar-header {
  width: 100%;
  padding: 20px 10px;
  text-align: right;
}
#navBar-header button {
  color: #fff;
  font-size: 20px;
}

#navBar-background {
  height: 100vh;
  display: flex;
  background-position: center bottom;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../../assets/imgs/DropDownMenu.jpg");
}

.social-media {
  width: 100%;
  display: block;
}
.social-media nav {
  width: 90%;
  border-bottom: 1px solid #fff;
}
.social-media nav ul li {
  display: inline-block !important;
}
.social-media nav ul li a {
  color: #fff;
  display: block;
  font-size: 20px;
  padding: 15px 20px;
  text-decoration: none;
}
